body {
    background-image: url("../assets/img.svg"), url("../assets/Ellipse\ 3.svg");
    background-repeat: no-repeat;
    background-size: auto 100%, 500px auto;
    background-position: left, right bottom;
    background-color: #0C172C;
    width: 100%;
    height: 100vh;
}
.parent {
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
}
.form-container h1 {
    color: white;
    font-size: 2.04rem;
    text-align: center;
}
u {
    text-decoration-color: #D0A920;
}
.form-container p {
    font-size: 1rem;
}
.form-container {
    width: 40%;
    margin-right: 1rem;
    color: white;
}
form {
    display: flex;
    flex-direction: column;
    align-items: center;
}
input[type=text], input[type=password] {
    width: 80%;
    display: inline-block;
    border: none;
    background: #0C172C;
    padding: 15px;
}
a {
    text-decoration: none;
    color: white;
}
.sign-in a {
    color: #D0A920;
}
.registerbtn {
    color: #D0A920;
    background-color: #27395B;
    border: none;
    text-align: center;
    width: 80%;
    padding-top: 25px;
    padding-bottom: 25px;
    border-radius: 10px;
    cursor: pointer;
}
label { 
    display:block;
    text-align: left; 
}
.message {
    background-color: black;
    color: white;
    width: fit-content;
    padding: 1rem;
    border-radius: 10px;
    margin: auto;
}
form span {
    display: flex;
    margin-bottom: 0.8rem;
    font-size: 0.7rem;
    color: red;
}
form p .btn {
    width: 24px;
    height: auto;
    color: #D0A920;
    cursor: pointer;
}
form .fa-solid {
    color: grey;
    width: 20px;
    height: auto;
}
.test-div {
    border-bottom: 1px solid grey;
    width: 80%;
    margin-top: 22px;
}
@media screen and (max-width: 1100px){
    body {
        background-image: url("../assets/Ellipse\ 3.svg");
        background-repeat: no-repeat;
        background-size: 300px auto;
        background-position: right bottom;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 100vh;
    }
    .form-container {
        width: 100%;
    }
    .test-div {
        border-bottom: 1px solid grey;
    }
}